import { mapState } from 'vuex';
import appTabNav from '../../../components/basic-component/app-tab-nav/app-tab-nav.vue';

export default {
    data() {
        return {
            theme: {
                color: '#ff4544'
            },
            tabList: [
                {id:-1, name: '全部'},
                {id:0, name: '待审核'},
                {id:1, name: '待打款'},
                {id:2, name: '已打款'},
                {id:3, name: '无效'},
            ],
            loading: null,
            list: [],
            activeTab: -1,
            noBorder: false,
            id: null,
            page: 2
        }
    },
    components: {
        'app-tab-nav': appTabNav,
    },
    computed: {
        ...mapState({
            mall: state => state.mallConfig.mall,
            custom_setting: state => state.mallConfig.share_setting_custom,
            share_setting: state => state.mallConfig.share_setting,
        })
    },
    methods: {
        open(e) {
            this.id = e;
        },

        tabStatus(e) {
            this.list = [];
            this.page = 2;
            this.activeTab = e.currentTarget.dataset.id;
            uni.showLoading({
                title: '加载中...'
            });
            this.getList();
        },
        getList() {
            let that = this;
            that.$request({
                url: that.$api.share.cash_list,
                data: {
                    status: that.activeTab
                },
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                if(response.code === 0) {
                    that.list = response.data.list;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                that.$hideLoading();
                uni.hideLoading();
                that.$event.on(that.$const.EVENT_USER_LOGIN).then(()=>{
                    that.getList();
                });
            });
        },
        getMore() {
            let that = this;
            uni.showLoading({
                title: '加载中...'
            });
            that.$request({
                url: that.$api.share.cash_list,
                data: {
                    status: that.activeTab,
                    page: that.page
                },
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    that.loading = null;
                    for (let i in response.data.list) {
                        for (let index in that.list) {
                            if(i == index) {
                                that.list[index].list = that.list[index].list.concat(response.data.list[index].list)
                            }
                        }
                    }
                    that.page++;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                uni.hideLoading();
            });
        },
        toGoods(id) {
            uni.navigateTo({
                url: `/pages/goods/goods?id=${id}`
            });
        },
    },

    onLoad() { this.$commonLoad.onload();
        let that = this;
        uni.setNavigationBarTitle({
            title: that.custom_setting.menus.cash.name
        });
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.getList();
    },
    onReachBottom() {
        this.getMore();
    }
}
